.App {
  text-align: center;
}

.main-container {
  width: 80% !important;
  max-width: 720px !important;
}

.greetings-header {
  text-align: right;
  color: #00adb5;
  margin-top: 2em;
}

.App-link {
  color: #61dafb;
}

.gamecard-title {
  text-align: right;
  font-size: 2.25rem;
  color: #eeeeee;
}

.gamecard-card {
  border-radius: 0 !important;
  background-color: #222831 !important;
  padding: 1em;
  margin: 2em;
}

.gamecard-card .card-body {
  padding-left: 0px;
}

.gamecard-text {
  color: #eeeeee;
  white-space: pre-line;
  text-align: right;
}

.btn {
  background-color: #00adb5 !important;
  color: #eeeeee;
  display: block !important;
  margin-bottom: 1em;
}

.btn:last-child {
  margin-bottom: 0;
}

.white-div {
  background-color: #eee;
  position: relative;
  width: 100%;
  margin-bottom: 3em;
}

.dark-div {
  background-color: #393e46;
  height: 100%;
  width: 100%;
  position: relative;
  top: 1em;
  left: 1em;
  overflow: auto;
}

.email-form {
  background-color: #222831;
  margin: 2em;
  overflow: auto;
  text-align: center;
}

.email-form .input-group {
  margin: 1em;
  width: auto;
}

.email-form .btn {
  margin-bottom: 1em;
  display: inline !important;
}

.email-form h2 {
  text-align: center;
  color: #eee;
  margin: 0.5em;
}

.contact-logos {
  margin: 1em;
}

.contact-logos img{
  width: 3em;
  margin-right: 0.5em;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
  margin-bottom: 1em;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}
